import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { ColorsServices } from '../../../services/colors/colors.services';
import { ActivatedRoute, Router } from '@angular/router';
import { ColorColumn } from './colors.column';
import { GridDataResult } from '@progress/kendo-angular-grid';
import { NbDialogService, NbToastrService } from '@nebular/theme';
import { FilterToQueryService } from '../../../services/filter-to-query/filter-to-query.service';
import { State } from '@progress/kendo-data-query';
import { saveAs } from 'file-saver';
import { SharedApiServiceService } from '../../../services/shared/shared-api-service.service';

@Component({
    selector: 'app-colors',
    templateUrl: 'colors.component.html',
    styleUrls: ['colors.component.scss'],
})
export class ColorsComponent implements OnInit {
    @ViewChild('dialog') dialog: any;

    columnGrid: any = ColorColumn;
    dataItem: GridDataResult = {
        data: [],
        total: 0,
    };
    lastMeta: string;
    loading: boolean;
    autoCompleteData: any = {
        name: 'category_brand',
        list: []
    };
    currentDeleteColor: any;
    isLoading: boolean = false;

    constructor(
        private colorsService: ColorsServices,
        private toaster: NbToastrService,
        // private activeRouter: ActivatedRoute,
        private convertFilter: FilterToQueryService,
        private router: Router,
        private dialogService: NbDialogService,
        private sharedService: SharedApiServiceService
    ) {
    }

    ngOnInit() {
        this.getColors();
    }

    getColors(meta?: string) {
        this.isLoading = true;
        this.colorsService.getColors(meta).subscribe(
            res => {
                this.dataItem.data = res['data'];
                for (let i = 0; i < this.dataItem.data.length; i++) {
                    this.dataItem.data[i]['nameEN'] = this.dataItem.data[i]['name_en'];
                    this.dataItem.data[i]['nameAr'] = this.dataItem.data[i]['name_'];
                }
                this.dataItem.total = res['total'];
            },
            error => {
                this.isLoading = false;
            },
            () => {
                this.isLoading = false;
            }
        );
    }

    deleteColor(dialogRef: any) {
        this.colorsService.deleteColor(this.currentDeleteColor.id).subscribe(
            res => {
                this.toaster.show(res['msg'], 'Success', { status: 'success' });
            },
            error => {
                this.toaster.show(error.error.message, 'Error', { status: 'danger' });
                dialogRef.close();
            },
            () => {
                dialogRef.close();
                this.getColors(this.lastMeta);
                this.currentDeleteColor = null;
            },
        );
    }

    commandActionClick(event: any) {
        switch (event.action.action) {
            case 'edit':
                this.router.navigate(['./colors/edit-color/' + event.data.id]);
                break;

            case 'delete':
                this.currentDeleteColor = event.data;
                this.open(this.dialog);
                break;
        }
    }

    open(dialog: TemplateRef<any>) {
        this.dialogService.open(dialog);
    }

    filterChange(event: State) {
        this.getColors(this.convertFilter.convertToQuery(event));
    }

    export() {
        this.sharedService.exportUrl = this.lastMeta ? 'colors/export/excel?' + this.lastMeta : 'colors/export/excel';
        this.sharedService.exportData(this.sharedService.exportUrl).subscribe(data => {
            saveAs(data, 'colors.xlsx');
        }),
            error => this.toaster.show('Error downloading the file.', 'Error', { status: 'danger' });
    }
}
