import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { SidebarComponent } from '../../shared/sidebar/sidebar.component';
import { ColorsComponent } from './index-color/colors.component';
import { EditColorComponent } from './edit-color/edit-color.component';

const routes: Routes = [{
  path: '',
  component: SidebarComponent,
  children: [
    { path: '', component: ColorsComponent },
    { path: 'create-color', component: EditColorComponent },
    { path: 'edit-color/:id', component: EditColorComponent },
  ],
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ColorsRoutingModule {
  static components = [
    ColorsComponent,
    EditColorComponent  
  ]
}
